html {
  height: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 320px;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "PTMono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  color: black;
  background: white;
  overflow-x: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

.wrapper {
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow-x: hidden;
}

main {
  flex-grow: 1;
}

.container {
  padding: 0 calc(50% - 660px);
}

.header {
  margin-bottom: 20px;
}
.header .container {
  padding: 0 calc(50% - 236px);
}

.header-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-nav {
  padding: 20px 20px;
}

.header-nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  min-height: 110px;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

.header-nav-item:not(:last-child) {
  margin-bottom: 15px;
}

.header-nav-link {
  display: block;
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
}
.header-nav-link:hover {
  transform: scale(1.2);
  transition: 0.2s;
  border-bottom: 1px solid orange;
}
.header-nav-link:focus {
  transform: scale(1.2);
  transition: 0.2s;
  border-bottom: 1px solid orange;
}
.header-nav-link:active {
  transform: scale(1);
  transition: 0.2s;
}

.intro {
  background: white url("../../img/bg/bg-main.png") no-repeat center;
  background-size: cover;
  min-height: 515px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 80px;
}

.intro h2 {
  margin: 0;
  padding: 0;
  font-size: 32px;
  font-weight: 700;
  letter-spacing: 0.01em;
  margin-bottom: 50px;
  color: black;
}

.intro-link {
  font-size: 32px;
  font-weight: 700;
  letter-spacing: 0.01em;
  background: #f8cc32;
  padding: 35px 8%;
}
.intro-link:hover {
  transform: scale(0.9);
  transition: 0.3s;
}
.intro-link:focus {
  transform: scale(0.9);
  transition: 0.3s;
}
.intro-link:active {
  transform: scale(0.9);
  transition: 0.3s;
}

.about {
  margin-bottom: 80px;
}

.about h2 {
  text-align: center;
  font-size: 24px;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 50px;
  background: #f8cc32;
  padding: 30px 0;
}

.about p {
  text-align: center;
  font-size: 20px;
  font-weight: 500;
  line-height: 32px;
}
.about p:last-of-type {
  margin-bottom: 50px;
}

.swiper-slide img {
  object-fit: cover;
  max-height: 212px;
}

.service h2 {
  margin: 0;
  padding: 0;
  text-align: center;
  font-size: 24px;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 50px;
  background: #f8cc32;
  padding: 30px 0;
}

.service h3 {
  margin: 0;
  padding: 0;
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  text-transform: uppercase;
  padding: 20px 0;
  cursor: pointer;
}
.service h3:hover {
  background: whitesmoke;
}

.service-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(441px, 1fr));
  align-items: start;
}

.service-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  border-radius: 5%;
  padding: 15px 15px 30px 40px;
  box-shadow: 0px 0px 3px grey;
  display: none;
}

.service-list__item {
  margin-bottom: 10px;
  font-size: 18px;
}

.service-list__item {
  line-height: 32px;
}

.service--nojs .service-list {
  display: block;
}

.service-list--active {
  display: block;
}

@media (max-width: 767px) {
  .service-wrapper {
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  }
}